.price {
  font-family: "opensans", "Arial", sans-serif;
  box-sizing: border-box;
  padding: 70px 0 100px 0;
  background: linear-gradient(176deg, #ffffff 7%, #f2f2f2 7%);

  @media (min-width: $width-tablet+px) {
    background: linear-gradient(176deg, rgba(255, 255, 255, 1) 11%, rgba(242, 242, 242, 1) 11%);
    padding: 127px 0 100px 0;
  }
}
.price__grid {
  padding: 0 2px 2px;
  // gap: 2px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: minmax(75px, auto) repeat(3, minmax(45px, auto));
}
.price__item {
  background-color: $color_white;
  width: 100%;
  height: 100%;
  border: 2px solid #e5e5e5;
  border-radius: 0;
  &:first-child {
    // grid-column-start: 2;
    // margin: -2px;
  }
}
.price__item--tarif {
  background: #283645;
  // border: 2px solid #283645;
  color: #ffffff;
  text-transform: uppercase;
  line-height: 30px;
  text-align: center;
  border: 2px solid #283645;
  box-sizing: border-box;
  z-index: 1;
}
.price__item--hit {
  position: relative;
  padding: 0 2px 2px;
  box-sizing: border-box;
  border-left-color: #e5e5e5;
  border-right-color: #e5e5e5;
  z-index: 5;
  overflow: hidden;
  &:after {
    position: absolute;
    content: "";
    background: #d22856;
    top: 8px;
    left: -16px;
    height: 18px;
    width: 75px;
    transform: rotate(-45deg);
    z-index: 5;
  }
  &:before {
    font-size: 14px;
    line-height: 14px;
    margin: 0;
    padding: 0;
    font-weight: normal;
    position: absolute;
    content: "ХИТ";
    height: 10px;
    transform: rotate(-45deg);
    top: 13px;
    left: 4px;
    z-index: 6;
  }
}
.price__item--empty {
  background-color: #f2f2f2;
  border: 0;
  margin-top: -2px;
  margin-left: -2px;
}
.price__item-title {
  margin: 0;
  padding: 0;
  font-weight: bold;
  font-size: 16px;
}
.price__item-price {
  margin: 0;
  padding: 0;
  font-weight: 300;
  font-size: 18px;
}
.price__item-text {
  // box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 45px;
  color: #283645;
  padding-left: 22px;
}
.price__item-yes {
  // box-sizing: border-box;
  font-size: 0;
  width: 100%;
  height: 100%;
  background: url("../img/icon-yes.svg") center no-repeat;
  background-size: 31px 21px;
}
.price__item-no {
  box-sizing: border-box;
  font-size: 0;
  width: 100%;
  height: 100%;
  background: url("../img/icon-no.svg") center no-repeat;
  background-size: 27px 27px;
}

table {
  padding: 0px;
  margin: 0px;
  width: 100%;
  // border-collapse: separate;
  // border-spacing: 2px;
  tr {
    display: flex;
    width: 100%;
    &:last-child {
      border-bottom: 2px solid #e5e5e5;
    }
  }
  td {
    border: 2px solid #e5e5e5;
  }
  .price__item {
    height: 45px;
    flex: 0 1 25%;
    box-sizing: border-box;
    background-color: white;
  }
  .price__item--tarif {
    height: 75px;
    background: #283645;
  }
  .price__item--empty {
    border: 0;
    background-color: transparent;
  }
  .price__item-title {
  }
  .price__item-price {
  }
  .price__item--hit {
  }
  .price__item-text {
  }
  .price__item-yes {
  }
  .price__item-no {
  }
}
