.contacts {
}
.contacts__container {
  padding-top: 50px;
  font-weight: 300;
  @media (min-width: $width-tablet+px) {
    padding-top: 55px;
  }
}
.contacts__title {
  margin: 0;
  padding: 0;
  font-weight: 300;
  font-size: 24px;
  line-height: 29px;
  margin-bottom: 45px;
  text-align: center;
  @media (min-width: $width-desktop+px) {
    font-size: 50px;
    line-height: 68px;
  }
}
.contacts__content {
  text-align: center;
  @media (min-width: $width-tablet+px) {
    display: flex;
    flex-wrap: wrap;
  }
}
.contacts__item {
  margin-bottom: 31px;
  @media (min-width: $width-tablet+px) {
    flex: 0 1 50%;
    margin-bottom: 55px;
  }
  &--w100 {
    @media (min-width: $width-tablet+px) {
      flex: 1 1 100%;
    }
  }
  @media (min-width: $width-desktop+px) {
    flex: 0 1 33%;
    &--order1 {
      order: -1;
    }
    &--order3 {
      order: 3;
    }
    &--w100 {
      order: 0;
    }
  }
}
.contacts__item-title {
  margin: 0;
  padding: 0;
  font-weight: bold;
  font-size: 18px;
  line-height: 30px;
}
.contacts__item-link {
  margin: 0;
  padding: 0;
  font-size: 24px;
  line-height: 33px;
  text-decoration: none;
  color: #d22856;
}
